import kleur from "kleur"
import Logger from "../../helpers/Logger.js"

const createHandle = (ctx, ctrl) => {
    return (exception, handler) => {
        if(ctx.exceptions[exception]) return Logger(`Handler for exception (${kleur.blue().bold(exception)}) in controller (path: ${kleur.blue().bold(ctrl.path)}, method: ${kleur.blue().bold(ctrl.method)}) has already been declared`, 'controller')
            
        ctx.exceptions[exception] = handler
    }
}

export default createHandle